$numCols: 6;
$col-width: 100/$numCols;

.fuel-depot {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    background: linear-gradient(30deg,
    rgba(200, 21, 21, 1) 25%,
    rgba(95, 2, 2, 1) 75%);
    overflow-x: hidden;
    animation: showCover 1s ease-out;
}

@keyframes showCover {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

.fuel-title {
    display: grid;
    font-family: 'Unlock', cursive;
    font-size: 4vw;
    justify-self: center;
    align-self: center;
    color:rgba(218, 125, 3, 0.7);
    text-transform: uppercase;
    text-shadow: -1px 1px 2px rgba(255, 96, 4, 0.7), 1px -1px 2px rgba(255, 96, 4, 0.7);
}

.type-symbol {
    width: 3vmin;
    display: inline-flex;
    align-self: center;
    background: linear-gradient(rgba(255, 128, 128, 0.4) 0%, rgba(255, 148, 148, 0.7) 100%);
    padding: 6px;
    border: 2px inset rgba(255, 145, 0, 0.7);
    border-radius: 6px;
    margin: auto 1vmin;
}

.fuel-in {
    display: flex;
    justify-content: space-evenly;
    align-self: center;
    margin: 1vh auto;
}

.fuel-input {
    background: linear-gradient(
        rgba(200, 21, 30, 0.9) 25%,
        rgba(146, 7, 2, 0.3) 75%);
    box-shadow: 1px 1px 9px rgba(219, 23, 33, 0.9);
    outline: double 1px rgb(255, 96, 96);
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    border-bottom: 3px solid rgb(254, 130, 130);
    padding: 0.1rem 0.9rem;
    width: 90px;
    color: white;
}

.fuel-input, .rando-button {
    margin: 0 1vmin;
}

.fuel-input::placeholder {
    color: white;
}

.output-button {
    border: unset;
    justify-self: center;
    align-self: center;
    border-radius: 0.3rem;
    margin: 0 1vmin;
    padding: 1vmin;
    background: radial-gradient(
            rgba(146, 7, 2, 0.2) 30%,
            rgba(200, 21, 30, 0.7) 90%);
    box-shadow: 1px 1px 6px rgba(219, 23, 33, 0.9);
    color: white;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    border-bottom: 3px solid rgb(254, 130, 130);
    border-top: 3px solid rgb(254, 130, 130);
    transition: all 300ms ease-out;
}

.output-button, .fuel-input {
    font-size: 2vmin;
}

.output-button:active {
    scale: calc(0.9);
    background: radial-gradient(30deg,
    rgba(146, 7, 2, 0.6) 15%,
    rgba(200, 21, 30, 1) 60%);
    box-shadow: -2px 2px 27px white, 2px -2px 27px white;
}

.fuelout {
    display: inline-flex;
    flex-wrap: wrap;
    width: 80vw;
    margin: 0.9rem auto;
    border-left: 1px ridge rgba(192, 135, 105, 0.9);
    border-bottom: 1px ridge rgba(192, 135, 105, 0.9);
    border-top: 1px ridge rgba(200, 84, 21, 0.9);
    border-right: 1px ridge rgba(200, 84, 21, 0.9);
    overflow: scroll;
    overflow-x: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
    justify-content: center;
    height: 75vh;
    color: rgb(255, 187, 0);
    font-family: 'Unlock', cursive;
    font-size: 2vw;
    line-height: 3vmin;
}

.fuelout::-webkit-scrollbar {
    display: none;
}

.output-button:hover {
    cursor: pointer;
    background: radial-gradient(30deg,
    rgba(146, 7, 2, 0.6) 15%,
    rgba(200, 21, 30, 1) 60%);
    text-shadow: -6px 6px 27px white, 6px -6px 27px white;
}

.default {
    display: flex;
    align-items: center;
}

.default:hover {
    .fuel-in {
        box-shadow: 0 27px 45px 9px white; 
    }
}

.ingredientMeal {
    padding: 0 2rem 2rem;
}

#randoMeal-instructions {
    padding: 0 2rem;
}

#randoMeal-name, #randoMeal-area-category {
    display: flex;
    justify-content: center;
}

.final-inspirational {
    display: flex;
    flex-wrap: wrap;
    color: rgb(212, 157, 16);
    text-shadow: -1px 1px 1px rgba(255, 96, 4, 0.7), 1px -1px 1px rgba(255, 96, 4, 0.7);
    font-family: 'Shrikhand', cursive;
    padding-left: 66vw;
    font-size: 2vw;
    animation: showMeal 1s ease-out;
}

.ingredientMealItem {
    animation: showMeal 1s ease-out;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@keyframes showMeal {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

.meal-img {
    border: 1px solid rgb(80, 15, 15);
    width: 20vw;
    justify-self: center;
}
    
.dish-title {
    display: grid;
    justify-content: center;
}

.mineral {
    background: linear-gradient(rgba(207, 108, 108, 0.3), rgba(206, 73, 73, 0.5));
    padding: 1vmin;
    border-bottom-right-radius: 1vmin;
    border-bottom-left-radius: 1vmin;
}

.mineral, .dosage {
    padding-left: 1rem;
}

.fuel-source {
    display: flex;
    flex-direction: column;
    background: linear-gradient(321deg, rgba(255,255,255,0) 25%, rgba(255, 255, 255, 0.2) 85%);
    font-family: 'Unlock', cursive;
    padding: 1vh 2vw;
    animation: showMeal 1s ease-out;
}

.fuel-sources-title {
    text-decoration: underline;
    font-size: 3vmin;
    display: flex;
    justify-content: center;
}

.randoMeal {
    animation: showMeal 1s ease-out;
}