:root {
  margin: 0;
  padding: 0;
  height: 100vh;
  min-width: 100vw;
  overflow-x: hidden;
  overflow-y: hidden;
  background: linear-gradient(20deg, #050f13 18%, #1D617A 58%, #050f13 60%, #1D617A 60%, #050f13 95%);
}

input {
  margin: 5px 4px;
  border: unset;
  border-bottom-style: ridge;
  border-bottom-width: 0.2rem;
  padding-left: 0.3rem;
}

input:focus {
  outline: none;
}

a {
  text-decoration: none;
}

body {
  margin: 0 auto;
  padding: 0;
}

li {
  list-style: none;
}

